import React from 'react';
import styled from 'styled-components';
import Button from './Button';

class Timer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            timer: false,
            time: 15
        }
        this.toggleTimer = this.toggleTimer.bind(this);
        this.timer = this.timer.bind(this);
    }
    timer() {
        if(this.state.timer) {
            
            this.countdown = setInterval(() => {
                if(this.state.time > 0) {
                    
                    this.setState((prevState) => ({ time: prevState.time - 1 }))
                } else {

                    this.setState(() => ({ timer: false }));
                    clearInterval(this.countdown);
                }
            }, 1000)
        } else {
            clearInterval(this.countdown);
        }
    }
    toggleTimer() {
        console.log('toggle', this.state.timer)
        this.setState((prevState) => ({ timer: !prevState.timer }));
        setTimeout(() => {

            this.timer()
        }, 1);
    }
    render() {

        const Container = styled.div`
            text-align: center;
            color: ${this.state.time <= 10 ? 'red' : 'black'};
        
            h1 {
                font-size: 200px;
                margin: 20px 0;
            }
        `;
        return (
            <Container>
                <h1>{this.state.time}</h1>
                <Button onClick={this.toggleTimer} text={this.state.timer ? 'Stop timer' : 'Start timer'} type="button" /> 
            </Container>
        )
    }
}

export default Timer;


